import { Component, Vue } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import { UserEntity } from '@/services/entities/user'
const beUser = namespace('beUser')
@Component
export default class BranchIdMixin extends Vue {
  @beUser.State('user') user!: UserEntity
  @beUser.State('role') roleMembers!: any
  get branchId(): number {
    return this.user.branchId
  }
  get brandName(): string {
    return this.user.branchName
  }
  get roleHOLEADERAmin(): boolean {
    let role = false
    if (this.user.role) {
      if (this.roleMembers.userRole.includes('HO_LEADER') || this.roleMembers.userRole.includes('HO_MEMBER') || this.roleMembers.userRole.includes('ADMIN')) {
        role = true
      }
    }
    return role
  }
}
