


































































import FilterSalary from '../components/filter/FilterApprove.vue'
import PopupFilter from '../components/filter/Filter.vue'
import PopupFilterAudit from '@/modules/audit/components/popup/AuditPopupFilter.vue'
import { Component, Mixins } from 'vue-property-decorator'
// import SalaryRepository from '@/services/repositories/salary'
import ContractRepository from '@/services/repositories/contract'
//@ts-ignore
import { PaginationInterfaceSalary } from '@/interface'
import { assign, forEach, trim } from 'lodash'
import DistrictMixin from '@/mixins/commonMixin'
import PopupMixin from '@/mixins/popup'
import PopupPayRollMonth from '../components/popup/PopupPayRollMonth.vue'
import PopupAproveSalary from '../components/popup/PopupAproveSalary.vue'

import getRepository from '@/services'
import SalaryRepository from '@/services/repositories/salary'
import UserMixin from '../mixins'
import { namespace } from 'vuex-class'
// const salaryReq: SalaryRepository = getRepository('salary')
const beAudit = namespace('beAudit')

@Component({
  components: { FilterSalary, PopupFilter, PopupFilterAudit, PopupPayRollMonth, PopupAproveSalary }
})
export default class SalaryApprove extends Mixins(DistrictMixin, PopupMixin, UserMixin) {
  @beAudit.Getter('getFilter') getFilter!: (rerport: string) => { contract: string; filters: Record<string, any> }

  data = []
  contractReq!: ContractRepository
  salaryReq!: SalaryRepository
  isLoading = false
  rowData: Record<string, any> = {}

  query: PaginationInterfaceSalary = {
    total: 100,
    page: 1,
    limit: 20,
    contractStatus: null,
    keywordString: '',
    orderByClause: 1
  }
  isExcelLoading = false
  isPdfLoading = false

  created(): void {
    this.contractReq = getRepository('contract', { districtId: this.districtId })
    this.salaryReq = getRepository('salary', { districtId: this.districtId })
  }

  mounted(): void {
    this.init()
  }

  get getIndex(): number {
    if (this.query.limit && this.query.page) {
      return this.query.limit * (this.query.page - 1) + 1
    }
    return 1
  }

  get getScreen(): string {
    if (window.screen.availWidth >= 1920) {
      return 'full'
    }
    if (window.screen.availWidth > 1366 && window.screen.availWidth < 1920) {
      return 'hd'
    }
    return 'low'
  }

  async init(): Promise<void> {
    try {
      this.isLoading = true
      //@ts-ignore
      const filters = this.getFilter(this.$route.name)
      if (filters) {
        this.query = assign(this.query, filters.filters)
      }
      let temp = {
        ...this.query,
        total: null,
        branchId: !this.roleHOLEADERAmin ? this.branchId : '',
        pageNo: this.query.page,
        pageSize: this.query.limit,
        keywordString: this.query.search,
        search: null
      }
      delete temp['page']
      delete temp['limit']
      const result = await this.contractReq.getPayRollSearch(temp)
      this.data = result.data
      const total = await this.contractReq.getPayRollCount(temp)
      this.query.total = total.data
      this.isLoading = false
    } catch (error) {
      this.isLoading = false
    }
  }
  handleOpenPayrollMonth(): void {
    // this.setOpenPopup({
    //   popupName: 'pay-roll-month',
    //   isOpen: true
    // })
    this.setOpenPopup({
      popupName: 'pay-roll',
      isOpen: true
    })
  }

  handleRowClick(data: Record<string, any>): void {
    this.rowData = data.row
    if (data.row.payrollStatus !== '1' && data.row.payrollStatus !== '2') {
      this.setOpenPopup({
        popupName: 'pay-roll',
        isOpen: true
      })
    } else {
      this.$message.error('Không xem được chi tiết khi chưa tính xong lương')
    }
  }

  handleSizeChange(size: number): void {
    this.query.limit = size
    this.init()
  }
  handleCurrentChange(page: number): void {
    this.query.page = page
    this.init()
  }
  handleOpenPopupFilter(): void {
    this.setOpenPopup({
      popupName: 'filter-salary',
      isOpen: true
    })
  }
  handleSort(command: number): void {
    this.query.orderByClause = command
    this.init()
  }
  handleSearch(params: Record<string, any>): void {
    // this.query.search = trim(str)
    this.query = {
      ...this.query,
      ...params
    }
    this.query.page = 1
    this.init()
  }

  handleFilter(data: Record<string, any>): void {
    const objectKeys = Object.keys(data)
    forEach(objectKeys, elm => {
      if (data[elm]) {
        this.query[elm] = data[elm]
      } else {
        if (this.query[elm]) {
          delete this.query[elm]
        }
      }
    })
    this.query.page = 1
    this.init()
  }

  handleLoad(): void {
    this.init()
  }
}
