





































































































import { Component, Mixins, Watch } from 'vue-property-decorator'
import BaseIcon from '@/components/base/icon/BaseIcon.vue'
import { forEach, debounce, assign, trim } from 'lodash'
import UserRepository from '@/services/repositories/user'
import getRepository from '@/services'
import UserMixin from '../../mixins'
import { namespace } from 'vuex-class'
export interface formDataInterface {
  payrollMonth: string | number
  payrollYear: string | number
  payrollStatus: string
  branchIdArray: any
  [x: string]: any
}
const beAudit = namespace('beAudit')

@Component({ components: { BaseIcon } })
export default class FilterSalary extends Mixins(UserMixin) {
  @beAudit.State('filters') filters!: Array<Record<string, any>>
  @beAudit.Action('setReportFilter') setReportFilter!: (data: { contract: string; filters: Record<string, any> }) => void
  @beAudit.Getter('getFilter') getFilter!: (rerport: string) => { contract: string; filters: Record<string, any> }

  members: Array<Record<string, unknown>> = []
  apiUser!: UserRepository
  filter: Record<string, any> = {
    search: '',
    orderByClause: 1
  }
  search = ''
  sorts: Array<Record<string, any>> = [
    {
      command: '1',
      label: 'Sắp xếp theo tháng',
      divided: false,
      active: true
    },
    {
      command: '2',
      label: 'Sắp xếp theo trạng thái',
      divided: true
    },
    {
      command: '3',
      label: 'Sắp xếp theo chi nhánh',
      divided: true
    }
  ]

  statusFilter: Array<Record<string, any>> = [
    {
      value: '1',
      label: 'Chờ xử lý'
    },

    {
      value: '2',
      label: 'Đang tính'
    },

    {
      value: '3',
      label: 'Đã tính xong'
    },

    {
      value: '4',
      label: 'Chờ duyệt'
    },
    {
      value: '5',
      label: 'CN đã duyệt'
    },
    {
      value: '6',
      label: 'CN từ chối'
    },
    {
      value: '7',
      label: 'Duyệt chi'
    },
    {
      value: '8',
      label: 'Hội sở từ chối'
    },
    {
      value: '9',
      label: 'Yêu cầu hiệu chỉnh'
    },
    {
      value: '10',
      label: 'Đồng ý hiệu chỉnh'
    },
    {
      value: '11',
      label: 'Từ chối hiệu chỉnh'
    },
    {
      value: '12',
      label: 'Yêu cầu hiệu chỉnh CN'
    },
    {
      value: '13',
      label: 'CN từ chối hiệu chỉnh'
    }
  ]
  isShow = false
  formFilterData: formDataInterface = {
    payrollMonth: new Date().getMonth(),
    payrollYear: new Date().getFullYear().toFixed(),
    payrollStatus: '',
    branchIdArray: []
  }
  branchList: any = []
  query = {
    search: '',
    type: 2,
    i: 0,
    districtId: 0,
    ps: 10
  }
  get getDisableArea(): boolean {
    if (this.roleHOLEADERAmin) {
      return false
    }
    return true
  }
  created(): void {
    this.apiUser = getRepository('user', {
      districtId: this.districtId
    })
    this.query = {
      search: '',
      type: 2,
      i: 0,
      districtId: this.districtId,
      ps: 10
    }
    if (this.getDisableArea) {
      this.branchList.push({
        id: this.user.branchId,
        locationCode: this.user.branchCode,
        name: this.user.branchName
      })
      this.formFilterData.branchIdArray = [this.branchId]
    }
    const filters = this.getFilter(this.$route.name as string)
    if (filters && filters.filters && filters.filters.search) {
      this.search = filters.filters.search
    } else {
      this.search = ''
    }
  }
  @Watch('search', { immediate: false }) handleSearch(keyword: string): void {
    const filters = this.getFilter(this.$route.name as string)
    if (filters) {
      this.filter = {
        ...filters.filters,
        ...this.filter,
        search: trim(this.search)
      }
    }
    if (this.search != filters?.filters?.search) {
      this.searchText(this.filter)
    }
    this.setReportFilter({
      contract: this.$route?.name as string,
      filters: {
        ...this.filter,
        search: trim(this.search)
      }
    })
  }

  searchText = debounce((value: Record<string, any>) => {
    this.$emit('search', value)
  }, 500)
  loadMoreBranch(): void {
    this.query.i += this.query.ps
    this.apiUser
      ?.getAreaList(this.query)
      .then(list => {
        this.branchList = [...this.branchList, ...list.data]
      })
      .catch(error => console.log(error))
  }
  async handleShowFilter(): Promise<void> {
    const filters = this.getFilter(this.$route?.name as string)
    await Promise.all([
      this.apiUser
        ?.getAreaList({
          search: '',
          type: 2,
          i: 0,
          districtId: this.districtId,
          ps: 10
        })
        .then(list => {
          this.branchList = list.data
        })
        .catch(error => console.log(error))
    ]).then(() => {
      if (filters) {
        this.formFilterData = assign(this.formFilterData, filters.filters)
      }
    })
  }
  remoteBranch(query: string): void {
    if (query == '') {
      this.apiUser
        ?.getAreaList({
          search: '',
          type: 2,
          i: 0,
          districtId: this.districtId,
          ps: 10
        })
        .then(list => {
          this.branchList = list.data
        })
        .catch(error => console.log(error))
    } else {
      this.apiUser
        ?.getAreaList({
          search: query,
          type: 2,
          i: 0,
          districtId: this.districtId,
          ps: 10
        })
        .then(list => (this.branchList = list.data))
        .catch(error => console.log(error))
    }
  }
  handleCommand(command: string): any {
    forEach(this.sorts, item => {
      if (item.command === command) {
        item.active = true
        this.$forceUpdate()
      } else {
        item.active = false
      }
    })
    this.$emit('sort', +command)
  }
  handleHideFilter(): void {
    console.log(this.formFilterData)
    const value = {
      ...this.formFilterData,
      branchIdArray: this.formFilterData.branchIdArray.join(',')
    }
    this.$emit('close', value)
  }
  resetData(): void {
    //@ts-ignore
    this.$refs.filterForm.clearValidate()
    this.formFilterData = {
      payrollMonth: new Date().getMonth(),
      payrollYear: new Date().getFullYear().toFixed(),
      payrollStatus: '',
      branchIdArray: this.getDisableArea ? [this.branchId] : []
    }
  }
  handleSubmit(type: 'reset' | 'submit'): void {
    if (type === 'reset') {
      this.resetData()
      return
    }
    this.setReportFilter({
      contract: this.$route?.name as string,
      filters: {
        ...this.formFilterData,
        search: trim(this.search)
      }
    })
    const value = {
      ...this.formFilterData,
      branchIdArray: this.getDisableArea ? this.branchId : this.formFilterData.branchIdArray.join(',')
    }
    this.isShow = false
    this.$emit('change', value)
  }
  handleOpenPopupFilter(): void {
    this.$emit('open')
  }
}
