






























import { Component, Mixins, Prop } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import { PAYROLL_EDIT } from '@/configs'
import DistrictMixin from '@/mixins/commonMixin'
import { AuthRepository } from '@/services/repositories/auth/index'
import ParamRepository from '@/services/repositories/params'
import ContractRepository from '@/services/repositories/contract'
import getRepository from '@/services'
import { forEach } from 'lodash'
import { filter } from 'lodash'

const authReq: AuthRepository = getRepository('auth')

@Component({})
export default class PopupRejectPay extends Mixins(PopupMixin, DistrictMixin) {
  @Prop({ required: true, type: Number, default: 0 }) id!: number
  @Prop({ required: true, type: Number, default: 0 }) status!: number
  contractReq!: ContractRepository
  apiParam!: ParamRepository
  rejectValue = ''
  rejectCode: any = ''
  paramsReject: any = []
  payrollList: any = []
  isValid = false
  created(): void {
    this.contractReq = getRepository('contract', {
      districtId: this.districtId
    })
  }

  hanleCancel(): void {
    this.setOpenPopup({
      popupName: 'popup-reject-pay-roll',
      isOpen: false
    })
    this.rejectValue = ''
  }

  handleChangeReason(): void {
    this.isValid = false
  }

  handlePayRoll(): void {
    if (!this.rejectCode) {
      this.isValid = true
      return
    }
    const arr = filter(this.paramsReject, (elm: any) => elm.id === this.rejectCode)
    let rejectCode: any = null
    if (arr.length) {
      //@ts-ignore
      rejectCode = arr[0].value
    }

    this.contractReq
      .updatePayRoll(this.id, {
        payrollStatus: this.status,
        rejectReason: this.rejectValue,
        rejectCode
      })
      .then(() => {
        this.showNotification(this.status)
        this.setOpenPopup({
          popupName: 'popup-reject-pay-roll',
          isOpen: false
        })
        this.setOpenPopup({
          popupName: 'pay-roll',
          isOpen: false
        })
        this.$emit('load')
        this.rejectValue = ''
        this.rejectCode = ''
        this.isValid = false
      })
  }
  handleConfirm(): void {
    if (!this.rejectCode) {
      this.isValid = true
      return
    }
    const arr = filter(this.payrollList, (elm: any) => elm.id === this.rejectCode)
    let rejectCode: any = null
    if (arr.length) {
      //@ts-ignore
      rejectCode = arr[0].value
    }

    this.contractReq
      .updatePayRoll(this.id, {
        payrollStatus: this.status,
        rejectReason: this.rejectValue,
        rejectCode
      })
      .then(() => {
        this.showNotification(this.status)
        this.setOpenPopup({
          popupName: 'popup-reject-pay-roll',
          isOpen: false
        })
        this.setOpenPopup({
          popupName: 'pay-roll',
          isOpen: false
        })
        this.$emit('load')
        this.rejectValue = ''
        this.rejectCode = ''
        this.isValid = false
      })
  }

  showNotification(status: number): void {
    switch (status) {
      case 6:
        this.$message.success('Từ chối thành công')
        break
      case 8:
        this.$message.success('Từ chối thành công')
        break
      case 11:
        this.$message.success('Từ chối hiệu chỉnh thành công')
        break
      case 13:
        this.$message.success('Từ chối hiệu chỉnh thành công')
        break
      case 9:
        this.$message.success('Yêu cầu hiệu chỉnh thành công')
        break
      case 12:
        this.$message.success('Yêu cầu hiệu chỉnh thành công')
        break
      default:
        break
    }
  }

  async handleOpen(): Promise<any> {
    const reason = ['Số liệu không đúng', 'Thiếu thông tin', 'Sai thông tin', 'Khác']
    if (this.status == 9 || this.status === 12) {
      this.payrollList = await authReq.getParamsReject(this.districtId, this.status)
      forEach(this.payrollList, (elm, index) => {
        //@ts-ignore
        elm.text = reason[index]
      })
    } else {
      try {
        this.paramsReject = await authReq.getParamsReject(this.districtId, this.status)
        forEach(this.paramsReject, (elm, index) => {
          //@ts-ignore
          elm.text = reason[index]
        })
      } catch (error) {
        console.log(error)
      }
    }
  }
  getPayroll(code: string): string {
    return PAYROLL_EDIT[code]
  }
  handleClose(): void {
    // @ts-ignore
    // this.$refs.formData.clearValidate()
    // console.log(2)
  }
}
