












































































import { Component, Prop, Vue } from 'vue-property-decorator'
import { forEach } from 'lodash'

@Component({
  components: {}
})
export default class PayRollTable extends Vue {
  @Prop({ required: true, type: Array, default: [] }) data!: Array<Record<string, any>>
  @Prop({ required: false, type: Object, default: {} }) query!: Record<string, any>
  @Prop({ required: false, type: Object, default: {} }) summary!: Record<string, any>
  @Prop({ required: false, type: Boolean, default: false }) isLoading!: boolean

  get getIndex(): number {
    return this.query.limit * (this.query.page - 1) + 1
  }

  handleSizeChange(val: number): void {
    this.$emit('sizeChange', val)
  }

  handleCurrentChange(val: number): void {
    this.$emit('currentChange', val)
  }

  summaryMethod(params: { columns: any; data: Array<Record<string, any>> }): any[] {
    const { columns } = params
    const sums: any[] = []
    forEach(columns, (column, index: number) => {
      if (index < 4) {
        sums[index] = ''
        return
      }
      if (index === 4) {
        sums[index] = 'Tổng'
        return
      }
      const a = this.summary[column.property]

      //@ts-ignore
      sums[index] = a ? this.$options.filters.formatNumber(a) : 0
      if (index === 14) {
        const column = this.summary['resultMonthRevenueTotalPercent']

        //@ts-ignore
        sums[14] = this.$options.filters.formatPercentAdv(column)
      }
      if (index === 6 || index === 13) {
        sums[index] = ''
      }
    })
    return sums
  }
}
