


















































































































































import { Component, Mixins, Prop } from 'vue-property-decorator'
import { forEach } from 'lodash'
import PopupMixin from '@/mixins/popup'
import { PaginationInterface } from '@/interface'
import getRepository from '@/services'
import BaseIcon from '@/components/base/icon/BaseIcon.vue'
import BaseTable from '@/components/base/table/BaseTable.vue'
import DistrictMixin from '@/mixins/commonMixin'
import { TabsFile } from '@/interface'
import PayRollTable from '../table/PayRollTable.vue'
import SalaryRepository from '@/services/repositories/salary'
import ContractRepository from '@/services/repositories/contract'
import PopupRejectPay from '../popup/PopupReject.vue'
import EmployeeRepository from '@/services/repositories/employee'
import TableOther from '../table/TableDVKD.vue'
import ParamRepository from '@/services/repositories/params'
import { filter } from 'lodash'

@Component({ components: { BaseTable, BaseIcon, PayRollTable, PopupRejectPay, TableOther } })
export default class PopupPayRoll extends Mixins(PopupMixin, DistrictMixin) {
  @Prop({ required: false, type: Object, default: {} }) detailFile!: Record<string, any>
  @Prop({ required: false, type: Boolean, default: false }) isError!: boolean
  employee!: EmployeeRepository
  contractReq!: ContractRepository
  salaryReq!: SalaryRepository

  paramsReq!: ParamRepository

  isShow = false
  detail: Record<string, any> = {}
  avatar = ''
  statusPayRoll = 0
  data: Array<Record<string, any>> = []
  tabs: Array<TabsFile> = [
    {
      id: 0,
      name: 'Cá nhân',
      active: true,
      label: 'CBNV'
    },
    {
      id: 1,
      name: 'Đơn vị kinh doanh',
      label: 'DVKD'
    }
  ]

  sheetAactive: any = 'CBNV'

  summary: Record<string, any> = {}

  isLoading = false
  isLoadingPopup = false
  isLoadingBtn = false

  query: PaginationInterface = {
    page: 1,
    limit: 20,
    total: 0
  }

  pageQuery: Record<string, any> = {
    pageIndex: 1,
    pageSize: 20
  }

  fromDate = 0
  toDate = 0

  get getTitleCode(): string {
    if (this.detailFile.payrollStatus === '6') {
      switch (this.detailFile.branchRejectCode) {
        case '1':
          return 'Sai nhân viên tư vấn'

        case '2':
          return 'Sai thông tin lương'
        case '3':
          return 'Sai mối quan hệ'
        case '4':
          return 'Sai đơn vị kinh doanh'
        case '5':
          return 'Sai chi nhánh'

        default:
          return 'Khác'
      }
    } else if (this.detailFile.payrollStatus === '8') {
      switch (this.detailFile.hoRejectCode) {
        case '1':
          return 'Sai nhân viên tư vấn'

        case '2':
          return 'Sai thông tin lương'
        case '3':
          return 'Sai mối quan hệ'
        case '4':
          return 'Sai đơn vị kinh doanh'
        case '5':
          return 'Sai chi nhánh'

        default:
          return 'Khác'
      }
    } else if (this.detailFile.payrollStatus === '12') {
      switch (this.detailFile.editRequestCode) {
        case '1':
          return 'Sai nhân viên tư vấn'

        case '2':
          return 'Sai thông tin lương'
        case '3':
          return 'Sai mối quan hệ'
        case '4':
          return 'Sai đơn vị kinh doanh'
        case '5':
          return 'Sai chi nhánh'

        default:
          return 'Khác'
      }
    } else {
      switch (this.detailFile.editRequestCode) {
        case '1':
          return 'Sai nhân viên tư vấn'

        case '2':
          return 'Sai thông tin lương'
        case '3':
          return 'Sai mối quan hệ'
        case '4':
          return 'Sai đơn vị kinh doanh'
        case '5':
          return 'Sai chi nhánh'

        default:
          return 'Khác'
      }
    }
  }

  get getRequestAtNvds(): boolean {
    const date = new Date(this.detailFile.editRequestedAt).getDate()
    return date > this.toDate
  }

  get getRequestAtDvkd(): boolean {
    const date = new Date(this.detailFile.editRequestedAt).getDate()
    return date > this.toDate
  }

  get getRequestAtHo(): boolean {
    const date = new Date(this.detailFile.editRequestedAt).getDate()
    return date <= this.toDate
  }

  getRoleButton(status: string): boolean {
    if (this.detailFile.payrollStatus === status) {
      return true
    }
    return false
  }

  created(): void {
    this.salaryReq = getRepository('salary', {
      districtId: this.districtId
    })
    this.contractReq = getRepository('contract', {
      districtId: this.districtId
    })
    this.employee = getRepository('employee', {
      districtId: this.districtId
    })
    this.paramsReq = getRepository('param', {
      districtId: this.districtId
    })
  }

  checkTime(): void {
    this.pageQuery.year = this.detailFile.payrollYear
    this.pageQuery.month = this.detailFile.payrollMonth
  }

  handleOpen(): void {
    // this.employee
    //   .getDetailEmployeeSalary(this.districtId, this.detailFile.approveRequestedBy)
    //   .then((res: any) => {
    //     this.avatar = res.avatar
    //     console.log('res', res)
    //   })
    //   .catch((err: any) => console.log('error', err))
    const date = new Date(this.detailFile.editRequestedAt).getDate()
    console.log('date', date)

    this.init()
    if (this.detailFile.payrollStatus === '9' || this.detailFile.payrollStatus === '5' || this.detailFile.payrollStatus === '12') {
      this.getRoleReqAt()
    }
  }

  async getRoleReqAt(): Promise<void> {
    try {
      const type = 'HO_APPROVE_DATE'
      const rs = await this.paramsReq.getParamTimeRequest(type)
      this.fromDate = +filter(rs, (elm: any) => elm.code === 'FROM_DATE')[0].value
      this.toDate = +filter(rs, (elm: any) => elm.code === 'TO_DATE')[0].value
    } catch (error) {
      console.log(error)
    }
  }

  init(isLoadingTable = false): void {
    this.checkTime()
    if (isLoadingTable) {
      this.isLoading = true
    } else {
      this.isLoadingPopup = true
    }
    const params = {
      ...this.pageQuery,
      branchIds: this.detailFile.branchId
    }
    if (this.sheetAactive === 'CBNV') {
      this.salaryReq
        .getSalariesIndividual(params)
        .then(res => {
          this.data = res.data
          if (isLoadingTable) {
            this.isLoading = false
          } else {
            this.isLoadingPopup = false
          }
        })
        .catch(() => {
          if (isLoadingTable) {
            this.isLoading = false
          } else {
            this.isLoadingPopup = false
          }
        })
      this.salaryReq.getSummaryIndividual(params).then(res => {
        this.query.total = res.data.total
        this.summary = res.data
      })
    } else {
      this.salaryReq
        .getSalariesOffice(params)
        .then(res => {
          this.data = res.data
          if (isLoadingTable) {
            this.isLoading = false
          } else {
            this.isLoadingPopup = false
          }
        })
        .catch(() => {
          if (isLoadingTable) {
            this.isLoading = false
          } else {
            this.isLoadingPopup = false
          }
        })
      this.salaryReq.getSummaryOffice(params).then(res => {
        this.query.total = res.data.total
        this.summary = res.data
      })
    }
  }

  resetQuery(): void {
    this.query = {
      page: 1,
      limit: 20,
      total: 0
    }
    this.pageQuery = {
      pageIndex: 1,
      pageSize: 20
    }
  }

  handleClose(): void {
    this.data = []
    this.detail = {}
    this.resetQuery()
    this.sheetAactive = 'CBNV'
    forEach(this.tabs, tab => {
      if (tab.id === 0) {
        tab.active = true
      } else {
        delete tab.active
      }
    })
  }

  handleChangeTab(item: Record<string, any>): void {
    this.data = []
    this.summary = {}
    this.isShow = true
    this.sheetAactive = item.label
    forEach(this.tabs, elm => {
      elm.active = elm.id === item.id
    })
    this.$forceUpdate()
    this.resetQuery()
    this.init(true)
  }
  handleSizeChange(size: number): void {
    this.query.limit = size
    this.pageQuery.pageSize = size
    this.init(true)
  }
  handleCurrentChange(page: number): void {
    this.query.page = page
    this.pageQuery.pageIndex = page
    this.init(true)
  }

  handleUpdateSalary(status: number): void {
    if (status === 6 || status === 8 || status === 9 || status === 12) {
      this.setOpenPopup({
        popupName: 'popup-reject-pay-roll',
        isOpen: true
      })
      this.statusPayRoll = status
    } else if (status === 11 || status === 13) {
      this.contractReq
        .updatePayRoll(this.detailFile.id, {
          payrollStatus: status,
          rejectReason: '',
          rejectCode: ''
        })
        .then(() => {
          this.$message.success('Từ chối hiệu chỉnh thành công')
          this.setOpenPopup({
            popupName: 'pay-roll',
            isOpen: false
          })
          this.handleEmitLoad()
        })
    } else {
      this.isLoadingBtn = true
      this.contractReq
        .updatePayRoll(this.detailFile.id, {
          payrollStatus: status,
          rejectReason: null
        })
        .then(() => {
          this.isLoadingBtn = false
          this.setOpenPopup({
            popupName: 'pay-roll',
            isOpen: false
          })
          this.showNotification(status)
          this.handleEmitLoad()
        })
        .catch(() => {
          this.isLoadingBtn = false
        })
    }
  }

  showNotification(status: number): void {
    switch (status) {
      case 4:
        this.$message.success('Yêu cầu duyệt thành công')
        break
      case 5:
        this.$message.success('Phê duyệt thành công')
        break
      case 7:
        this.$message.success('Duyệt chi thành công')
        break
      case 9:
        this.$message.success('Yêu cầu hiệu chỉnh thành công')
        break
      case 10:
        this.$message.success('Duyệt hiệu chỉnh thành công')
        break
      default:
        break
    }
  }
  handleEmitLoad(): void {
    this.$emit('load')
  }
  handeLoad(): void {
    this.handleEmitLoad()
  }
}
